.row {
    max-width: $grid-width; // maximum width achievable, meaning that even if more is available
                        // the row won't get larger than this value.
    margin: 0 auto; // center it, making the left and right values equals;

    // select everything except the last child
    &:not(:last-child) {
        margin-bottom: $gutter-vertical;
    }

    @include clearfix;

    // attribute here, select all elements that starts(^) with "col-";
    [class^="col-"] {
        float: left;

        &:not(:last-child){
            margin-right: $gutter-horizontal;

        }
    }

    // OR
    //margin-bottom: $gutter-vertical;
    //
    //&:last-child {
    //    margin-bottom: 0;
    //}

    .col-1-of-2{
        width: calc((100% - #{$gutter-horizontal}) / 2);
    }

    .col-1-of-3{
        width: calc((100% - 2 * #{$gutter-horizontal}) / 3);
    }

    .col-2-of-3{
        width: calc(2 * ((100% - 2 * #{$gutter-horizontal}) / 3) + #{$gutter-horizontal});
    }

    .col-1-of-4{
        width: calc((100% - 3 * #{$gutter-horizontal}) / 4);
    }

    .col-2-of-4{
        width: calc(2 * ((100% - 3 * #{$gutter-horizontal}) / 4) + #{$gutter-horizontal});
    }

    .col-3-of-4{
        width: calc(3 * ((100% - 3 * #{$gutter-horizontal}) / 4) + (2 * #{$gutter-horizontal}));
    }

}